<template>
  <div class="home">
    <div class="try">
      <v-toolbar class="toolbarPage teal lighten-4 black--text">
        <v-spacer></v-spacer>

        <v-toolbar-title>Entrenamiento</v-toolbar-title>

        <v-spacer></v-spacer>
      </v-toolbar>

      <v-card class="cardConvocatoria">
        <div>
          <h3 style="text-decoration:underline; margin-bottom:10px">Datos del Entrenamiento</h3>
        </div>
        <div id="itemsCardConvocatoria">
          <div class="infoConvocatoria">
            <p style="margin-right: 10px; text-decoration:underline">Día:</p>
            <p>{{day}}</p>
          </div>
          <div class="infoConvocatoria">
            <p style="margin-right: 10px; text-decoration:underline">Hora:</p>
            <p>{{hour}}</p>
          </div>
        </div>
      </v-card>

      <div class="try2">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">- VOY ({{playersVoy.length}}) -</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="i in playersVoy.length" :key="i">
                <td style="text-align:center">{{playersVoy[i-1]}}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>

        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">- NO VOY ({{playersNoVoy.length}}) -</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="i in playersNoVoy.length" :key="i">
                <td style="text-align:center">{{playersNoVoy[i-1]}}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>

        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">- DUDA ({{playersDuda.length}}) -</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="i in playersDuda.length" :key="i">
                <td style="text-align:center">{{playersDuda[i-1]}}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </div>

      <v-card class="cardConvocatoria" style="width:40vh">
        <v-select v-model="selectedPlayer" label="Escoger jugador" :items="playersListNames"></v-select>

        <v-row justify="center">
          <v-dialog v-model="dialog" persistent max-width="290" :disabled="!isselected">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                style="width:90%"
                v-bind="attrs"
                v-on="on"
              >Confirmar Asistencia</v-btn>
            </template>
            <v-card>
              <v-card-title class="headline">Marca tu asistencia al entrenamiento</v-card-title>
              <v-card-text>¿Vas a asistir al entrenamiento?</v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="green darken-1" text @click="assist(0)">Voy</v-btn>
                <v-btn color="green darken-1" text @click="assist(1)">No Voy</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-card>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import info from "@/information";
import fb from "@/fb";

export default {
  watch: {
    selectedPlayer: function() {
      if (this.selectedPlayer != "") {
        this.isselected = true;
      } else {
        this.isselected = false;
      }
    }
  },
  data: function() {
    return {
      dialog: false,
      playersListNames: [],
      playersVoy: [],
      playersNoVoy: [],
      playersDuda: [],
      playersIDsFB: [],
      day: null,
      hour: null,
      match: null,
      selectedPlayer: null,
      isselected: false
    };
  },
  methods: {
    assist: function(type) {
      this.dialog = false;

      if (type == 0) {
        let index = this.lookforPosition(this.selectedPlayer);
        console.log(index);

        fb.db
          .collection("Entrenamiento")
          .doc(this.playersIDsFB[index])
          .update({
            Status: 0
          });
      } else if (type == 1) {
        let index = this.lookforPosition(this.selectedPlayer);
        fb.db
          .collection("Entrenamiento")
          .doc(this.playersIDsFB[index])
          .update({
            Status: 1
          });
      }

      this.update();
    },
    update: function() {
      (this.playersListNames = []),
        (this.playersVoy = []),
        (this.playersNoVoy = []),
        (this.playersDuda = []),
        (this.playersIDsFB = []),
        fb.db.collection("Entrenamiento").onSnapshot(res => {
          const changes = res.docChanges();

          changes.forEach(change => {
            if (change.type === "added") {
              let t = change.doc.data();

              let name = this.lookforNamefromNum(t.PlayerNum);
              this.playersListNames.push(name);
              this.playersIDsFB.push(change.doc.id);

              switch (t.Status) {
                case 0:
                  this.playersVoy.push(name);
                  break;

                case 1:
                  this.playersNoVoy.push(name);
                  break;

                case 2:
                  this.playersDuda.push(name);
                  break;

                default:
                  break;
              }
            }
          });
        });
    },
    add: function() {
      for (let i = 0; i < info.playersList.length; i++) {
        let p = {
          PlayerNum: info.playersList[i].playerNumber,
          Status: 2
        };

        fb.db.collection("Entrenamiento").add(p);
      }
    },
    lookforPosition: function(name) {
      let i = 0;
      let found = false;
      let size = this.playersListNames.length;

      while (i < size && !found) {
        if (this.playersListNames[i] == name) {
          found = true;
        } else {
          i++;
        }
      }

      if (found) {
        return i;
      }
    },
    lookforNamefromNum: function(num) {
      let i = 0;
      let found = false;
      let size = info.playersList.length;

      while (i < size && !found) {
        if (info.playersList[i].playerNumber == num) {
          found = true;
        } else {
          i++;
        }
      }

      if (found) {
        return info.playersList[i].playerName;
      }
    }
  },
  created: function() {
    this.update();

    this.day = info.currentInfo.currentDay;
    this.hour = info.currentInfo.currentHour;
    this.match = info.currentInfo.currentMatch;
  }
};
</script>
