<template>
  <div class="stats">
          <v-toolbar class="toolbarPage teal lighten-4 black--text">
        <v-spacer></v-spacer>

        <v-toolbar-title>Estadísticas</v-toolbar-title>

        <v-spacer></v-spacer>
      </v-toolbar>

    <v-card style="width: 400px" class="hidden-md-and-up">
    <v-card-title>
      <v-text-field
        v-model="statsSearch"
        append-icon="mdi-magnify"
        label="Buscar jugador"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      hide-default-footer
      mobile-breakpoint="0"
      :headers="statsHeaders"
      :items="statsTable"
      :search="statsSearch"
      :sort-by="['playerNum']"
      :sort-desc="[false]"
      :footer-props="{'items-per-page-options':[-1]}"
    ></v-data-table>
  </v-card> 

  <v-card style="width: 1000px" class="hidden-sm-and-down">
    <v-card-title>
      <v-text-field
        v-model="statsSearch"
        append-icon="mdi-magnify"
        label="Buscar jugador"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      hide-default-footer
      mobile-breakpoint="0"
      :headers="statsHeaders"
      :items="statsTable"
      :search="statsSearch"
      :sort-by="['playerNum']"
      :sort-desc="[false]"
      :footer-props="{'items-per-page-options':[-1]}"
    ></v-data-table>
  </v-card>

  <p id="updateStatsText">Actualizado a: {{dateStats}}</p> 

  </div>
</template>

<script>
// @ is an alias to /src

import fb from '@/fb'
import info from '@/information'

export default {
  data: function(){
    return{
      dateStats: null,
      statsSearch: '',
      statsHeaders: [
        { text: 'Num', 
        value: 'playerNum',
        align: 'start',
        sortable: false,
        },
        {
          text: 'Jugador',
          sortable: false,
          value: 'playerName',
        },
        { text: 'Goles', value: 'goalsPlayer' },
        { text: 'Amarillas', value: 'yellowCardPlayer' },
        { text: 'Rojas', value: 'redCardPlayer' },
        ],
      statsTable: [
      ]
    }
  },
  methods: {
    searchPlayer: function(id){
      let found = false;
      let i = 0;
      let size = info.playersList.length;
      let name = "No disponible";

      while(i<size && !found){
        if(info.playersList[i].playerNumber == id){
          found = true;
          name = info.playersList[i].playerName;
        }
        else{
          i++;
        }
      }

      return name;
    }
  },
  created: function(){

    this.dateStats = new Date().toLocaleString('en-GB');

    fb.db.collection('Stats').onSnapshot(res => {
      const changes = res.docChanges();

      changes.forEach(change => {
        
        if(change.type === 'added'){
          let t = change.doc.data();
          let name = this.searchPlayer(t.playerNum);
          let obj = {
            playerNum: t.playerNum,
            playerName: name,
            goalsPlayer: t.goalsPlayer,
            yellowCardPlayer: t.yellowCardPlayer,
            redCardPlayer: t.redCardPlayer
          }
          
          if(name != "No disponible"){
          this.statsTable.push(obj);
          }
        }
      });
    })
  }
}
</script>
