<template>
  <div class="settings">
    <h1>TroyaWEB (Settings)</h1>

    <div class="try" v-if="toShow">
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="sendNotifications">Enviar Notificaciones</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="updateHomeScore">Actualizar Resultado Home</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="updateJornadaScore">Actualizar Resultado Jornada</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="changeMatchHour">Cambiar Hora Partido</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="changeTrainingHour">Cambiar Hora Entreno</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="changeComment">Cambiar Comentario</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="resetConvocatoria">Resetear Convocatoria</v-btn>
      <v-btn class="btnStyled light-green lighten-2 black--text"
      @click="resetEntrenamiento">Resetear Entrenamiento</v-btn>
    </div>
    <div class="try">
      <v-alert v-model="showError" type="error">Acceso no autorizado. Inicie sesión</v-alert>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import info from '@/information'

export default {
  methods:{
    sendNotifications:function(){},
    updateHomeScore:function(){},
    updateJornadaScore:function(){},
    changeMatchHour:function(){},
    changeTrainingHour:function(){},
    changeComment:function(){},
    resetConvocatoria:function(){},
    resetEntrenamiento:function(){},
  },
  data: function() {
    return {
      toShow: null,
      showError: null,
    };
  },
  mounted: function(){
    if(info.logInformation.logStatus){
      this.toShow=true;
      this.showError=false;
    }
    else{
      this.toShow=false;
      this.showError=true;
    }
  }
};
</script>
